import React, { useState } from 'react'
import { KeyboardDatePicker } from '@material-ui/pickers'
import { makeStyles } from '@material-ui/core/styles'

const useStyle = makeStyles({
  inputFecha: {
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: 'white',
    height: '50px',
    overflow: 'hidden',
  }
})

function FormReservas() {

  const [fecha, setFecha] = useState(new Date());
  const classes = useStyle();

  return (
    <>
      <div className="contenedorFormulario">
        <h3>Reserva un día para el corte</h3>
        <form className="formularioReservas">
          <div className="filaFormulario">
            <span>NOMBRE</span>
            <input type="text" name="nombre" placeholder="Ingrese su nombre" required />
          </div>
          <div className="filaFormulario">
            <span>CELULAR</span>
            <input type="tel" name="telefono" placeholder="Ingrese su WhatsApp" required />
          </div>
          <div className="filaFormulario">
            <span>ELIGE EL DÍA</span>
            {<KeyboardDatePicker
              autoOk
              className={classes.inputFecha}
              minDate={new Date()}
              format="dd/MM/yyyy"
              value={fecha}
              InputAdornmentProps={{ position: "start" }}
              onChange={date => setFecha(date)}
            />}
          </div>
          <div className="filaFormulario">
            <span>ELIGE EL HORARIO</span>
            <select className="form-input select-filter" data-placeholder="9:00" required>
              <option>&nbsp;</option>
              <option value="1">13:30</option>
              <option value="2">14:00</option>
              <option value="3">14:30</option>
              <option value="4">15:00</option>
              <option value="5">15:30</option>
            </select>
          </div>
          <button className="reservar" type="submit">Reservar</button>
        </form>
      </div>
    </>
  )
}

export default FormReservas
